* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #101010;
  color: floralwhite;
}

.main {
  display: flex-row;
  width: auto;
  border: 1px solid black;
  margin: 45px;
  border-radius: 45px;
  background-color: #252525;
}
.search-box {
  display: flex;
  justify-content: center;
  margin-top: 100px;
  background-color: #252525;
  border: #353535;
}
.search-txt {
  text-indent: 15px;
  padding: 5px;
  border: 1px solid #353535;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  font-weight: 700;
  background-color: #353535;
  color: #858585;
}
.search-txt:focus {
  border: #353535;
  outline: none;
}
.search-button {
  display: flex;
  padding: 20px;
  border: 1px solid #353535;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  background-color: #353535;
  color: #858585;
}
.search-button:hover {
  cursor: pointer;
}
.search-button:active {
  color: #fff;
  font-weight: bold;
  background-color: #890092;
  font-family: "Inter", Noto, sans-serif;
}
.token-box {
  margin: auto;
  margin-top: 100px;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 70%;

  background-color: #009289;
  border-radius: 48px;
}
.token {
  width: 180px;
  height: 180px;
  color: red;
  margin: 50px;
  border-radius: 40px;
  object-position: 10px 50px;
}

.token-text {
  position: relative;
  top: 185px;
  left: 9px;
  background-color: #890092;
  text-align: center;
  font-family: "Inter", Noto, sans-serif;
  color: #fff;
  padding: 3px;
  border-radius: 20px;
  font-weight: bold;
  width: 90%;
  box-shadow: 0 0 2px 1px #009289 inset;
}

.search {
  display: flex-row;
}
.lds-grid {
  position: relative;
  margin: auto;
  top: 200px;
  width: 80px;
  height: 80px;
}
.lds-grid div {
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  animation: lds-grid 1.2s linear infinite;
}
.lds-grid div:nth-child(1) {
  top: 8px;
  left: 8px;
  animation-delay: 0s;
}
.lds-grid div:nth-child(2) {
  top: 8px;
  left: 32px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(3) {
  top: 8px;
  left: 56px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(4) {
  top: 32px;
  left: 8px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(5) {
  top: 32px;
  left: 32px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(6) {
  top: 32px;
  left: 56px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(7) {
  top: 56px;
  left: 8px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(8) {
  top: 56px;
  left: 32px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(9) {
  top: 56px;
  left: 56px;
  animation-delay: -1.6s;
}
@keyframes lds-grid {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.loader {
  position: absolute;
  bottom: 50px;
  background-color: red;
}
